import React from "react";
function Header() {
  return (
    <>
      <header id="mad-header" className="mad-header header-2">
        <div className="mad-header-section--sticky-xl">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-xl-3 col-lg-4 col-sm-3 col-6">
                <a href="index.html" className="mad-logo">
                  <img src="/img/logo.png" alt />
                </a>
              </div>
              <div className="col-xl-6 col-lg-4 col-sm-3 col-6">
                {/*================ Navigation ================*/}
                <nav className="mad-navigation-container">
                  <ul className="mad-navigation mad-navigation--vertical-sm">
                    <li className="">
                      <a href="#">Home</a>
                    </li>
                    <li className="menu-item ">
                      <a href="#">Shop</a>
                    </li>
                    <li className="menu-item ">
                      <a href="#">Pages</a>
                    </li>
                    <li className="menu-item ">
                      <a href="#">Elements</a>
                    </li>
                    <li className="menu-item ">
                      <a href="#">Blog</a>
                    </li>
                  </ul>
                </nav>
                {/*================ End of Navigation ================*/}
              </div>
              <div className="col-xl-3 col-lg-4 col-sm-6">
                <div className="mad-icon-boxes our-info item-col-1">
                  <div className="mad-col mad-header-sticky">
                    {/*================ Icon Box ================*/}
                    <article className="mad-icon-box">
                      <i className="mad-icon-box-icon">
                        <img className="svg" src="/img/support.svg" alt />
                      </i>
                      <div className="mad-icon-box-content">
                        <span className="mad-icon-sub-title">
                          24/7 Customer Support:
                        </span>
                        <h4 className="mad-icon-box-title">+1 800 559 6580</h4>
                      </div>
                    </article>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
}

export default Header;
